body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 4px;
}

@media (min-width: 780px) and (min-height: 420px) {
  html {
    font-size: 6px;
  }    
}

@media (min-width: 1040px) and (min-height: 560px) {
  html {
    font-size: 8px;
  }    
}

@media (min-width: 1300px ) and (min-height: 700px) {
  html {
    font-size: 10px;
  }    
}

@media (min-width: 1560px) and (min-height: 840px) {
  html {
    font-size: 12px;
  }    
}

@media (min-width: 1820px) and (min-height: 980px) {
  html {
    font-size: 14px;
  }    
}

@media (min-width: 2080px) and (min-height: 1120px) {
  html {
    font-size: 16px;
  }    
}

@media (min-width: 2340px) and (min-height: 1260px) {
  html {
    font-size: 18px;
  }    
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.screen {
  width: 100vw;
  height: 100vh;
  background-color: black;
}

#canvas {
  width: 100%;
  height: 100%;
}

#message {
  z-index: 3;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  font-family: monospace;
  font-size: 3rem;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#message > * {
  user-select: none;
}

#message > div {
  margin-top: 1em;
  margin-bottom: 1em;
}
